:root {
  --primiry-text-color: #434455;
  --title-text-color: #2e2f42;
  --accent-color: #3997ff;
  --primary-bg-color: #f5f5f5;
  --white-color: #fff;
  --secondary-title-color: #f4f4fd;
  --secondary-text-color: #e7e9fc;
  --company-color: #8e8f99;
  --success-color: #31d0aa;
  --modal-color: #fcfcfc;
  --pressed-state: #404bbf;
  --main-transition: 250ms cubic-bezier(.4, 0, .2, 1);
}

.header {
  box-shadow: 0 2px 1px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 1px 6px rgba(46, 47, 66, .08);
}

.logo {
  color: var(--title-text-color);
  letter-spacing: .03em;
  text-transform: uppercase;
  font-family: Raleway, sans-serif;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.16667;
}

.logo .logo-purple {
  color: var(--accent-color);
  font-weight: 800;
}

@media screen and (max-width: 767.98px) {
  .header {
    padding-top: 24px;
    padding-bottom: 24px;
  }

  .site-nav, .auth-nav {
    display: none;
  }

  .btn-menu-hamburger {
    background-color: rgba(0, 0, 0, 0);
    border: none;
    padding: 0;
    line-height: 0;
  }

  .icon-menu-hamburger {
    stroke: var(--title-text-color);
  }

  .main-nav {
    justify-content: space-between;
    display: flex;
  }

  .mobile-menu {
    z-index: 1000;
    width: 100%;
    height: 100%;
    background-color: var(--white-color);
    visibility: hidden;
    pointer-events: none;
    opacity: 0;
    transition: opacity var(--main-transition), visibility var(--main-transition);
    position: fixed;
    top: 0;
    left: 0;
    overflow-y: scroll;
    box-shadow: 0 2px 1px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 1px 6px rgba(46, 47, 66, .08);
  }

  .is-open {
    visibility: visible;
    pointer-events: visible;
    opacity: 1;
  }

  .mobile-menu-container {
    height: 100vh;
    flex-direction: column;
    justify-content: space-between;
    padding-bottom: 40px;
    padding-left: 40px;
    display: flex;
    position: relative;
  }

  .menu-close-btn {
    width: 24px;
    height: 24px;
    background-color: var(--secondary-text-color);
    color: #000;
    cursor: pointer;
    transition: background-color var(--main-transition), color var(--main-transition);
    border: 1px solid rgba(0, 0, 0, .1);
    border-radius: 50%;
    justify-content: center;
    align-items: center;
    display: flex;
    position: absolute;
    top: 24px;
    right: 20px;
  }

  .menu-icon-close {
    width: 8px;
    height: 8px;
    fill: currentColor;
  }

  .menu-close-btn:hover, .menu-close-btn:focus {
    background-color: var(--accent-color);
    color: var(--white-color);
  }

  .mobile-nav {
    padding-top: 80px;
  }

  .mobile-site-nav .link.current {
    color: var(--accent-color);
  }

  .mobile-site-nav > .mobile-item:not(:last-child) {
    margin-bottom: 40px;
  }

  .mobile-site-nav-link {
    color: var(--title-text-color);
    letter-spacing: .02em;
    transition: color var(--main-transition);
    font-size: 36px;
    font-style: normal;
    font-weight: 600;
    line-height: 1.11111;
  }

  .mobile-site-nav-link:hover, .mobile-site-nav-link:focus {
    color: var(--accent-color);
  }

  .tel-link {
    color: var(--accent-color);
    font-size: 36px;
    font-weight: 600;
    line-height: 1.11111;
  }

  .mobile-auth-nav {
    margin-bottom: 48px;
  }

  .mobile-auth-nav > .mobile-item:not(:last-child) {
    margin-bottom: 40px;
  }
}

@media screen and (min-width: 768px) {
  .mobile-menu, .btn-menu-hamburger {
    display: none;
  }

  .main-nav, .main-site-nav {
    align-items: center;
    display: flex;
  }

  .site-nav {
    margin-left: 121px;
    display: flex;
  }

  .site-nav .link.current {
    color: var(--accent-color);
  }

  .site-nav > .item:not(:last-child) {
    margin-right: 40px;
  }

  .overlay {
    position: relative;
  }

  .current:after {
    content: "";
    width: 100%;
    height: 4px;
    background: var(--accent-color);
    border-radius: 2px;
    position: absolute;
    bottom: 0;
    left: 0;
  }

  .site-nav-link {
    color: var(--title-text-color);
    letter-spacing: .02em;
    transition: color var(--main-transition), background-color var(--main-transition), box-shadow var(--main-transition);
    padding-top: 24px;
    padding-bottom: 24px;
    font-style: normal;
    font-weight: 600;
    line-height: 1.5;
    display: block;
  }

  .site-nav-link:hover, .site-nav-link:focus, .site-nav-link:active {
    color: var(--accent-color);
  }

  .auth-nav {
    margin-left: auto;
    padding-top: 24px;
    padding-bottom: 12px;
  }

  .auth-nav > .item:not(:last-child) {
    margin-bottom: 4px;
  }

  .auth-nav-link {
    color: var(--primiry-text-color);
    letter-spacing: .04em;
    transition: color var(--main-transition), background-color var(--main-transition), box-shadow var(--main-transition);
    font-size: 12px;
    line-height: 1.33333;
    display: block;
  }

  .auth-nav-link:hover, .auth-nav-link:focus, .auth-nav-link:active {
    color: var(--accent-color);
  }
}

@media screen and (min-width: 1200px) {
  .site-nav {
    margin-left: 90px;
  }

  .auth-nav {
    align-items: center;
    padding-bottom: 24px;
    display: flex;
  }

  .auth-nav-link {
    letter-spacing: .02em;
    font-size: 16px;
    line-height: 1.5;
  }

  .auth-nav > .item:not(:last-child) {
    margin-bottom: 0;
    margin-right: 40px;
  }
}

