@import './base';
@import './common';

@import './header';
@import './hero';
@import './quality';
@import './bicycles';
@import './snowboards';
@import './accessories.scss';
@import './our-gallery.scss';
@import './brands';
@import './why-us.scss';
@import './location';
@import './footer';

body {
  font-family: 'Roboto', sans-serif;
  color: var(--primiry-text-color);
}
.link {
  text-decoration: none;
}

.container {
  padding-left: 15px;
  padding-right: 15px;
}
@media screen and (min-width: 428px) {
  .container {
    margin-left: auto;
    margin-right: auto;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.social-list {
  display: flex;
  justify-content: space-between;
}
@media screen and (max-width: 767.98px) {
  .mobile-contacts .social-list {
    align-items: center;
    max-width: 328px;
  }
}
.social-link {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  background-color: var(--accent-color);
}
.social-icon {
  display: block;
  width: 16px;
  height: 16px;
  fill: var(--white-color);
}
@media screen and (max-width: 767.98px) {
  .mobile-contacts .social-icon {
    width: 24px;
    height: 24px;
  }
}

